.dpik__search{
    margin: 4rem 0;
    text-align: center;
    
}
.dpik__search h2{
    font-family: var(--font-family);
    font-size: 4rem;
    font-weight: 600;
    text-transform: capitalize;
    margin-bottom: 2rem ;
}
.dpik__search-input{
    width: 40%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.dpik__search-input input{
    width: 100%;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.5rem;
    border-radius:10px 0 0 10px;
    border: 2px solid gray;
    outline: none;
    padding: 0.5rem;
    color: gray;
}
.dpik__search-input button{
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 1.5rem;
    letter-spacing: 1px;
    border-radius: 0 10px 10px 0;
    border: 2px solid gray;
    outline: none;
    padding: 0.5rem;
    background: var(--color-bg);
    color: white;
    cursor: pointer;
}

.dpik__search-imgContainer{
    columns:3 ;
    gap: 4px;
    padding: 2rem 4rem;
}
