@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap');


:root {
  --font-family: 'Poppins', sans-serif;

  --color-bg: linear-gradient(90deg, rgba(0, 188, 212, 1), rgba(63, 81, 181, 1));
  --color-bg-hover: linear-gradient(-90deg, rgba(0, 188, 212, 1), rgba(63, 81, 181, 1));
  --gradient-text: linear-gradient(45deg, rgba(238, 130, 238, 1), rgba(0, 0, 255, 1));
  --color-footer: #00171F;
  --shadow: 5px 0 10px rgba(0, 0 , 0, 0.6)
}
